import React, { Component } from 'react';

// Tela de carregamento
//  exibida enquanto o carregamento da API do Google Maps  feita
export default class Loading extends Component {
  render() {
    const hide = this.props.loaded ? ' loading-hide' : '';
    let message;
    if(this.props.loadError) {
      message = (
        <p className='loading-subtitle error'>
          No foi possivel carregar o mapa.
          <br/>
          Recarregue a pgina.
        </p>
      );
    } else {
      message = <p className='loading-subtitle'>Carregando...</p>;
    }
    return (
      <article className={'loading' + hide}>
        <h1 className='loading-title'>
          Goinia
          {message}
        </h1>
      </article>
    );
  }
}



