import { map } from './map';
import { places } from '../data/places_data';

// Funo de callback para ser executada quando a API do Google Maps
// terminar de carregar.
function initMap() {
  this.init();
  this.panToPlaces();
  this.onGoogleMapsLoad();
}

function googleError() {
  this.onGoogleError();
}

// Torna a funo de inicializao global e vinculada ao mapa.
window.initMap = initMap.bind(map);
window.googleError = googleError.bind(map);

// Minha APIKey para usar a API do Google.
const APIKey = 'AIzaSyA7oz8Q4iD0yb1Qkokep8DAz78j27XjpfQ';

// Adiciona o script da API no html e inicialia o carregamento.
// Quando concluido a funo initMap  executada e as funcionalidades
// da aplicao so ativadas.
export function loadGoogleMapsAPI() {
  const script = document.createElement("script");
  const url = 'https://maps.googleapis.com/maps/api/js'
  script.src = `${url}?key=${APIKey}&callback=initMap`;
  script.async = true;
  script.defer = true;
  script.onerror = window.googleError;
  document.body.appendChild(script);
}



