import React, { Component } from 'react';

// Boto para exibir a barra de navegao do aplicativo
export default class SideBarToggle extends Component {
  render() {
    return (
      <a href="#" className="menu" onClick={this.props.onToggle}>&#9776;</a>
    );
  }
}



